    /*landing page CSS*/
  .hero-section {
    height: 70vh;
    padding-top: 10rem;
    background-color: rgba(219, 252, 255, 1);
    z-index: -1;
    margin-top:-20px;
  }
  .hero-section .flex-hero {
    flex-direction: column;
    gap: 30px;
    text-align:center;
  }
  
  .hero-section .flex-hero h1 {
    font-size: 65px;
    font-weight: 900;
  }
  .text-gradient {
    background: linear-gradient(45deg,rgba(57, 232, 219, 1) 2.34%,rgba(32, 130, 123, 1) 100.78%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-top:50px;
  }
  .hero-section h4 {
    font-size: 22px;
    font-weight: 300;
    text-align: center;
    padding: 12px 14rem;
    color:#969BA5;
  }
  
  .hero-section .button {
    text-align: center;
  }
  
  .hero-section div:nth-child(2) {
    margin-top: 4rem;
  }
  
  .hero-section .image {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0px;
  }
  
  .cross{
    margin-top: -80px;
  }
  
  
