  /* This is the CSS for the Login form*/
  body{
    background-color: #076e74;
    color:white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: large;
  }
  
  .container {
    margin-top: 10%;
    text-align: center;
    width:100%;
  } 
  .form-group {
    width:90%;
    display:block;
    margin:auto;
    margin-bottom:20px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    text-align: justify;
  }
  
  .form-control {
    width: 100%;
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .login {
    color: #fff;
    margin-left:150px;
    margin-right:50px;
    background-color: rgba(79, 171, 177, 0.64);
  }
  
  .reset {
    background-color: rgba(241, 155, 155, 1);
    color: #fff;
    margin-right: 250px;
    height: 40px;
    width:20%;
    margin-left:150px;
  }
  .reset:hover{
    background-color: rgba(177, 79, 85, 0.89)
  }
  .log-in{
    background-color: rgba(79, 171, 177, 0.64);
    color:white;
    margin-left:250px;
    width:20%;
    height: 40px;
  }
  .log-in:hover{
    background-color: rgba(79, 171, 177, 0.34);
  }
  .reset-signup{
    background-color: rgba(241, 155, 155, 1);
    color: #fff;
    margin-right: 250px;
    height: 40px;
    width:20%;
    margin-left:150px;
  }
  
  .reset-signup:hover{
    background-color: rgba(177, 79, 85, 0.89);
  }
  
  .submit-signup{
    background-color: rgba(79, 171, 177, 0.64);
    color:white;
    margin-left:250px;
    width:20%;
    height: 40px;
  }
  .submit-signup:hover{
    background-color: rgba(79, 171, 177, 0.34);
  }
  .signup-text{
    font-size:large;
  }
  
  
  @keyframes blobani {
    0% {top: 0; left: 0; transform: scale(1);}
    30% {top: 0; left: 0; transform: scale(1.2);}
    60% {top: 0; left: 0; transform: scale(1.3);}
    100% {top: 0; left: 0; transform: scale(1);}
  }